import { create } from "zustand";
import { persist } from "zustand/middleware";
import { type Language, settings } from "@/config/settings";
import { translationLogger } from "@/utils/translationLogger";

// ÏßÑÌñâ Ï§ëÏù∏ Î≤àÏó≠ Î°úÎî© PromiseÎì§ÏùÑ Ï∂îÏ†ÅÌïòÎäî Îßµ
const loadingPromises = new Map<Language, Promise<void>>();

// URLÏóêÏÑú ÌòÑÏû¨ Ïñ∏Ïñ¥ Í∞ÄÏ†∏Ïò§Í∏∞
const getCurrentLanguageFromPath = (): Language => {
  if (typeof window === "undefined") return settings.languages.default;

  const pathSegments = window.location.pathname.split("/");
  const urlLang = pathSegments[1] as Language;

  if (urlLang && settings.languages.supported.includes(urlLang)) {
    return urlLang;
  }

  return settings.languages.default;
};

// Ï¥àÍ∏∞ Ïñ∏Ïñ¥ ÏÑ§Ï†ïÏùÄ Ìï≠ÏÉÅ URL Í≤ΩÎ°úÏóêÏÑú Í∞ÄÏ†∏Ïò¥ (ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎßå)
const initialLanguage: Language = (() => {
  if (typeof window === "undefined") return settings.languages.default;
  return getCurrentLanguageFromPath();
})();

interface LanguageState {
  currentLanguage: Language;
  translations: Record<Language, Record<string, string>>;
  isLoading: boolean;
  error: string | null;
  isTranslationLoaded: Record<Language, boolean>;
  isHydrated: boolean;
  t: (key: string, args?: Record<string, string>) => string;
  setLanguage: (lang: Language) => Promise<void>;
  syncLanguageWithPath: () => Promise<void>;
  loadTranslations: (lang: Language) => Promise<void>;
  setCurrentLang: (lang: Language) => void;
  setHydrated: (hydrated: boolean) => void;
}

/**
 * Î°úÏª¨ Î≤àÏó≠ ÌååÏùº Î°úÎìú
 */
async function loadLocalTranslations(
  lang: Language,
): Promise<Record<string, string> | null> {
  if (typeof window === "undefined") return null;
  
  try {
    const url = `/locales/${lang}.json`;
    console.log(`üîÑ Loading local translations from: ${url}`);
    
    const response = await fetch(url);
    console.log(`üì• Response status for ${lang}:`, response.status, response.ok);
    
    if (!response.ok) {
      throw new Error(`Failed to load local translations for ${lang}: ${response.status}`);
    }
    
    const translations: Record<string, string> = await response.json();
    console.log(`‚úÖ Loaded ${Object.keys(translations).length} translation keys for ${lang}`);
    
    return translations;
  } catch (error) {
    console.warn(`‚ùå Local translations not found for ${lang}:`, error);
    return null;
  }
}

export const useLanguageStore = create<LanguageState>()(
  persist(
    (set, get) => ({
      currentLanguage: initialLanguage,
      translations: settings.languages.supported.reduce((acc, lang) => {
        acc[lang] = {};
        return acc;
      }, {} as Record<Language, Record<string, string>>),
      isLoading: false,
      error: null,
      isHydrated: false,
      isTranslationLoaded: settings.languages.supported.reduce((acc, lang) => {
        acc[lang] = false;
        return acc;
      }, {} as Record<Language, boolean>),
      setLanguage: async (lang: Language) => {
        if (!settings.languages.supported.includes(lang)) {
          console.warn(`Unsupported language: ${lang}`);
          return;
        }

        const { currentLanguage } = get();
        console.log(`üîÑ setLanguage called: ${lang}`);
        translationLogger.logLanguageSync(currentLanguage, lang, 'setLanguage');
        
        set({ currentLanguage: lang });

        // Î≤àÏó≠Ïù¥ Î°úÎìúÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Î°úÎìú
        const { isTranslationLoaded } = get();
        if (!isTranslationLoaded[lang]) {
          await get().loadTranslations(lang);
        }
      },
      syncLanguageWithPath: async () => {
        if (typeof window === "undefined") return;
        
        const { isHydrated } = get();
        
        // hydrationÏù¥ ÏôÑÎ£åÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ ÎåÄÍ∏∞
        if (!isHydrated) {
          console.log('üîÑ [syncLanguageWithPath] Waiting for hydration to complete...');
          return;
        }
        
        const langFromPath = getCurrentLanguageFromPath();
        const { currentLanguage, isTranslationLoaded } = get();
        
        console.log(`üîÑ [syncLanguageWithPath] Current: ${currentLanguage}, Path: ${langFromPath}`);
        
        if (langFromPath !== currentLanguage) {
          console.log(`üîÑ [syncLanguageWithPath] Language mismatch detected, updating store from ${currentLanguage} to ${langFromPath}`);
          translationLogger.logLanguageSync(currentLanguage, langFromPath, 'syncLanguageWithPath');
          
          // Ïñ∏Ïñ¥ ÏÉÅÌÉú Ï¶âÏãú ÏóÖÎç∞Ïù¥Ìä∏
          set({ currentLanguage: langFromPath });
          
          // Î≤àÏó≠Ïù¥ Î°úÎìúÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ÏóêÎßå Î°úÎìú
          if (!isTranslationLoaded[langFromPath]) {
            console.log(`üîÑ [syncLanguageWithPath] Loading translations for ${langFromPath}`);
            await get().loadTranslations(langFromPath);
          } else {
            console.log(`‚úÖ [syncLanguageWithPath] Translations for ${langFromPath} already loaded`);
          }
        } else {
          console.log(`‚úÖ [syncLanguageWithPath] Language already synchronized: ${currentLanguage}`);
          
          // Ïñ∏Ïñ¥Îäî ÎßûÏßÄÎßå Î≤àÏó≠Ïù¥ Î°úÎìúÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
          if (!isTranslationLoaded[currentLanguage]) {
            console.log(`üîÑ [syncLanguageWithPath] Loading missing translations for ${currentLanguage}`);
            await get().loadTranslations(currentLanguage);
          }
        }
      },
      setHydrated: (hydrated: boolean) => {
        console.log(`üîÑ Setting hydration status: ${hydrated}`);
        set({ isHydrated: hydrated });
      },
      t: (key: string, args?: Record<string, string>) => {
        const { translations, currentLanguage, isHydrated, isTranslationLoaded, isLoading } = get();

        // hydrationÏù¥ ÏôÑÎ£åÎêòÏßÄ ÏïäÏïòÍ±∞ÎÇò Î≤àÏó≠ Î°úÎî© Ï§ëÏù∏ Í≤ΩÏö∞ Îπà Î¨∏ÏûêÏó¥ Î∞òÌôò
        if (!isHydrated || isLoading) {
          return '';
        }

        // ÌòÑÏû¨ Ïñ∏Ïñ¥Ïùò Î≤àÏó≠Ïù¥ ÏïÑÏßÅ Î°úÎìúÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ Îπà Î¨∏ÏûêÏó¥ Î∞òÌôò
        if (!isTranslationLoaded[currentLanguage]) {
          return '';
        }

        // ÌòÑÏû¨ Ïñ∏Ïñ¥Ïùò Î≤àÏó≠ Ï∞æÍ∏∞
        const currentTranslations = translations[currentLanguage] || {};
        let translation = currentTranslations[key];

        // Î≤àÏó≠Ïù¥ ÏóÜÎäî Í≤ΩÏö∞ Í∏∞Î≥∏ Ïñ∏Ïñ¥ÏóêÏÑú Ï∞æÍ∏∞
        if (!translation && currentLanguage !== settings.languages.default) {
          const defaultTranslations = translations[settings.languages.default] || {};
          translation = defaultTranslations[key];
          
          if (translation) {
            // Í∏∞Î≥∏ Ïñ∏Ïñ¥ÏóêÏÑú Ï∞æÏùÄ Í≤ΩÏö∞ Î°úÍπÖ
            translationLogger.logMissingTranslation(
              key, 
              currentLanguage, 
              'fallback_to_default', 
              translation
            );
          }
        }

        // Ïó¨Ï†ÑÌûà Î≤àÏó≠Ïù¥ ÏóÜÎäî Í≤ΩÏö∞ Ï≤òÎ¶¨
        if (!translation) {
          translationLogger.logMissingTranslation(key, currentLanguage, 'no_translation_found');
          
          // Î≤àÏó≠Ïù¥ ÏôÑÏ†ÑÌûà Î°úÎìúÎêú ÏÉÅÌÉúÏóêÏÑú ÌÇ§Í∞Ä ÏóÜÎäî Í≤ΩÏö∞ÏóêÎßå ÌÇ§ Î∞òÌôò (Í∞úÎ∞úÏö©)
          // ÌîÑÎ°úÎçïÏÖòÏóêÏÑúÎäî Îπà Î¨∏ÏûêÏó¥ Î∞òÌôò
          if (process.env.NODE_ENV === 'development') {
            return `[${key}]`; // Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎäî ÌÇ§Î•º ÎåÄÍ¥ÑÌò∏Î°ú Í∞êÏã∏ÏÑú ÌëúÏãú
          }
          return ''; // ÌîÑÎ°úÎçïÏÖòÏóêÏÑúÎäî Îπà Î¨∏ÏûêÏó¥
        }

        // Î≥ÄÏàò ÏπòÌôò
        if (args && typeof translation === "string") {
          Object.entries(args).forEach(([argKey, value]) => {
            translation = translation.replace(`{${argKey}}`, value);
          });
        }

        return translation;
      },
      loadTranslations: async (lang: Language) => {
        // ÏÑúÎ≤Ñ ÏÇ¨Ïù¥ÎìúÏóêÏÑúÎäî Î°úÎî©ÌïòÏßÄ ÏïäÏùå
        if (typeof window === "undefined") {
          return;
        }

        const { isTranslationLoaded, translations } = get();

        // Ïù¥ÎØ∏ ÏßÑÌñâ Ï§ëÏù∏ Î°úÎî©Ïù¥ ÏûàÎäîÏßÄ ÌôïÏù∏
        if (loadingPromises.has(lang)) {
          console.log(`üîÑ Translation loading already in progress for ${lang}, waiting...`);
          return loadingPromises.get(lang);
        }

        // Í∞ïÏ†úÎ°ú Îã§Ïãú Î°úÎìúÌïòÍ±∞ÎÇò Ïù¥ÎØ∏ Î°úÎìúÎêú Í≤ΩÏö∞ Ï≤¥ÌÅ¨
        const hasTranslations = translations[lang] && Object.keys(translations[lang]).length > 0;
        const isMarkedAsLoaded = isTranslationLoaded[lang];
        
        if (isMarkedAsLoaded && hasTranslations) {
          console.log(`‚úÖ Translations for ${lang} already loaded with ${Object.keys(translations[lang]).length} keys, skipping`);
          return;
        }
        
        if (isMarkedAsLoaded && !hasTranslations) {
          console.log(`‚ö†Ô∏è ${lang} marked as loaded but has no translations, force reloading...`);
        }

        console.log(`üîÑ Starting to load translations for ${lang}`);
        
        // Promise ÏÉùÏÑ± Î∞è Ï∫êÏã±
        const loadingPromise = (async () => {
          try {
            set({ isLoading: true, error: null });

            let translationsData: Record<string, string> = {};

            // Î°úÏª¨ Î≤àÏó≠ ÌååÏùº Î°úÎìú
            try {
              const localTranslations = await loadLocalTranslations(lang);
              if (localTranslations) {
                translationsData = { ...translationsData, ...localTranslations };
                console.log(`‚úÖ Local translations loaded for ${lang}:`, Object.keys(localTranslations).length, 'keys');
                translationLogger.logTranslationSuccess(lang, Object.keys(localTranslations).length, 'local');
              }
            } catch (error) {
              translationLogger.logLoadingError(lang, error as Error, 'local');
            }

            // Î≤àÏó≠Ïù¥ ÏóÜÎäî Í≤ΩÏö∞ Í∏∞Î≥∏ Ïñ∏Ïñ¥Î°ú fallback
            if (Object.keys(translationsData).length === 0 && lang !== settings.languages.default) {
              console.warn(
                `No translations found for ${lang}, falling back to ${settings.languages.default}`,
              );
              try {
                const defaultTranslations = await loadLocalTranslations(settings.languages.default);
                if (defaultTranslations) {
                  translationsData = defaultTranslations;
                  translationLogger.logTranslationSuccess(lang, Object.keys(defaultTranslations).length, 'local');
                }
              } catch (error) {
                translationLogger.logLoadingError(settings.languages.default, error as Error, 'local');
              }
            }

            console.log(`üéâ Final translations for ${lang}:`, Object.keys(translationsData).length, 'keys');

            set((state) => ({
              translations: {
                ...state.translations,
                [lang]: translationsData,
              },
              isLoading: false,
              isTranslationLoaded: {
                ...state.isTranslationLoaded,
                [lang]: true,
              },
            }));

            console.log(`‚úÖ Translation loading completed for ${lang}`);
          } catch (error) {
            console.error(`Failed to load translations for ${lang}:`, error);
            translationLogger.logLoadingError(lang, error as Error, 'local');
            
            set({
              error: error instanceof Error
                ? error.message
                : "Failed to load translations",
              isLoading: false,
            });
          } finally {
            // Promise Ï∫êÏãúÏóêÏÑú Ï†úÍ±∞
            loadingPromises.delete(lang);
          }
        })();

        // Promise Ï∫êÏã±
        loadingPromises.set(lang, loadingPromise);
        
        return loadingPromise;
      },
      setCurrentLang: (lang) => set({ currentLanguage: lang }),
    }),
    {
      name: "language-storage",
      partialize: (state) => ({
        currentLanguage: state.currentLanguage,
      }),
      onRehydrateStorage: () => (state) => {
        if (state && typeof window !== "undefined") {
          console.log('üîÑ Rehydrating language store:', state.currentLanguage);
          
          // hydration ÏôÑÎ£å ÌëúÏãú
          state.setHydrated(true);
          
          // Î¶¨ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÌõÑ ÌòÑÏû¨ Ïñ∏Ïñ¥Ïùò Î≤àÏó≠Ïù¥ Î°úÎìúÎêòÏßÄ ÏïäÏïòÎã§Î©¥ Î°úÎìú
          // setTimeoutÏùÑ ÏÇ¨Ïö©ÌïòÏó¨ Î†åÎçîÎßÅ ÏÇ¨Ïù¥ÌÅ¥Í≥º Î∂ÑÎ¶¨
          if (!state.isTranslationLoaded[state.currentLanguage]) {
            console.log('üîÑ Loading translations after rehydration');
            setTimeout(() => {
              state.loadTranslations(state.currentLanguage);
            }, 0);
          }
        }
      },
    },
  ),
);
